import { useEffect, useState } from "react";
import styled from "styled-components"

export default function Page2({ page }) {
    const [transX, setTransX] = useState(500);
    const [opacity, setOpacity] = useState(0);

    useEffect(() => {
        if (page === 1) {
            setTransX(0);
            setOpacity(1);
        }
        else {
            setTransX(500);
            setOpacity(0);
        }
    }, [page])

    return (<Page>
        <h3>/ </h3>
        <Container>
            <Box area="one" transX={transX} opacity={opacity} delay={0.3}>
                <p><strong>1.</strong>     </p>
                <span>: </span>
                <p>1&#41;     </p>
                <p>2&#41;    2      </p>
                <span>: </span>
                <p>1&#41;  HTTP  HTML PC  </p>
                <p>2&#41;  (cheerio)     </p>
                <span>: </span>
                <p>1&#41; HTTP request headers User-Agent  Windows </p>
                <p>2&#41;  parsing     (parse5) ,    </p>
                <p>:    ,    1  </p>
            </Box>
            <Box area="two" transX={transX} opacity={opacity} delay={0.45}>
                <p><strong>2.</strong> CORS Proxy   </p>
                <p>:     CORS   </p>
                <p>:      Origin  </p>
                <p>:  cors-anywhere  Heroku  ,  </p>
                <p>: CORS   HTTP   </p>
                <p>:           </p>
            </Box>
            <Box area="three" transX={transX} opacity={opacity} delay={0.6}>
                <p><strong>3.</strong> Firebase CLI Github actions   CI/CD  </p>
                <p><strong>4.</strong> Vue.js      </p>
            </Box>
        </Container>
    </Page>)
}

const Page = styled.div`
    width: 100%;
    height: 100vh;
    h3 {
        color: #e98203;
        margin: 0;
        padding-top: 8vh;
    }
`;

const Container = styled.div`
    display: grid;
    width: fit-content;
    height: 80vh;
    margin: auto;
    margin-top: 2vh;
    grid-template-areas: "one two" "one two" "one three";
    text-align: left;
    line-height: 24px;
    strong {
        color: dimgray;
    }
`

const Box = styled.div`
    display: inline-block;
    width: 80%;
    margin: 20px auto;
    background-color: rgba(255, 255, 255, 0.85);
    color: black;
    padding: 5px 15px;
    grid-area: ${props => props.area};
    transform: translateX(${props => props.transX}px);
    opacity: ${props => props.opacity};
    transition-duration: 0.8s;
    transition-delay: ${props => props.delay}s;
`