import { useEffect, useState } from "react";
import styled from "styled-components"

export default function Page4({ page }) {
    const [transX, setTransX] = useState(500);
    const [opacity, setOpacity] = useState(0);

    useEffect(() => {
        if (page === 3) {
            setTransX(50);
            setOpacity(1);
        }
        else {
            setTransX(500);
            setOpacity(0);
        }
    }, [page])

    return (<Page>
        <LineBox>
            <h1>Introduce.</h1>
            <p>   .   .</p>
            <RightLine transX={transX} opacity={opacity}>-       . Frontend  React Vue.js   API, Firebase,  , TypeScript, CI/CD           .</RightLine>
            <LeftLine transX={transX} opacity={opacity}>-      .           ,            .</LeftLine>
            <RightLine transX={transX} opacity={opacity}>-     ,    .   (, , ,  )                .</RightLine>
            <LeftLine transX={transX} opacity={opacity}>-      .                     (carousel, fullpage, cheerio)        ,  CS     .</LeftLine>
        </LineBox>
    </Page>)
}

const Page = styled.div`
    width: 100%;
    height: 100vh;
    position: relative;
`;

const LineBox = styled.div`
    width: fit-content;
    height: fit-content;
    position: absolute;
    top: 50%;
    left: 50%;
    line-height: 24px;
    transform: translate(-50%, -50%);
    h1 {
        color: #e98203;
    }
    p {
        font-size: 18px;
        font-weight: 500;
    }
`

const LeftLine = styled.div`
    width: 600px;
    height: auto;
    margin: auto;
    padding: 15px;
    margin: 20px;
    opacity: ${props => props.opacity};
    transform: translateX(-${props => props.transX}px);
    transition-duration: 1s;
    background-color: rgba(255, 255, 255, 0.85);
    color: black;
`;

const RightLine = styled.div`
    width: 600px;
    height: auto;
    margin: auto;
    padding: 15px;
    margin: 20px;
    opacity: ${props => props.opacity};
    transform: translateX(${props => props.transX}px);
    transition-duration: 1s;
    background-color: rgba(255, 255, 255, 0.85);
    color: black;
`;